.swep
{



}


.swep .swiper {
  width: 80%;
  padding-top: 50px;
  height: 67%;
  
 
}



.swep .swiper-slide {
  background-position: center;
  background-size: cover;
  width: 250px;
  
}

.swep .swiper-slide img {
  display: block;
  width: 100%;
  -webkit-box-reflect: below 1px linear-gradient(transparent, transparent , #0002 ,#0004 );
  box-sizing: border-box;
  font-family: sans-serif;
  border-radius: 4px;
  opacity: 0.8;
border: 1px solid rgba(24, 24, 24, 0.493);
  
}



.swep .swiper-slide img:active {

  
border: 1px solid rgba(243, 241, 241, 0.493);
  rotate: 10deg;
  transition: 1s;
  opacity: 1;
  scale: 1.07;
}
