
section .flex{


  justify-content: space-around;
}
.icon-envelope
{

color: var(--subtitle);
margin-right: 1rem;
font-size: 1.8rem;

}



.title
{

  font-size:2.1rem;
  color: var(--title);
  margin-bottom: 1rem;


}


.subtitle
{
color: var(--subtitle);
 margin-bottom: 2rem;
 line-height: 1.65rem;

}

 label
{


color: var(--subtitle);

}

#email , #message
{
    all:unset;
    background-color: rgba(63, 63, 70, 0.15);
    border: 1px  solid rgb(63, 63, 70);
    width: 16rem;
    padding: 0.5rem 1rem;
    margin-left: 1rem;
    border-radius: 5px;
    transition: 0.3s;
    font-size: 1.1rem;
    color: #fff;
}


.ligh section .contacts-us #email ,
.ligh section .contacts-us #message
{
    all:unset;
    background-color: rgba(63, 63, 70, 0.15);
    border: 1px  solid rgb(63, 63, 70);
    width: 16rem;
    padding: 0.5rem 1rem;
    margin-left: 1rem;
    border-radius: 5px;
    transition: 0.3s;
    font-size: 1.1rem;
    color: #111;
}








#email:focus , #message:focus
{

border: 1px solid rgb(9, 9, 9);


}



#email:hover , #message:hover
{

border: 1px solid rgb(8, 8, 8);


}

#message
{
    min-height: 9rem;
    resize: vertical;

}


.submit
{
  width: 50px;
  background-color: rgb(83, 83, 82);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px;
  border: 1px solid black;
  height: 30px;
  font-weight: 700;
  color: #111;
  transition: 1s;
  
}



.submit:hover
{

scale:0.97


}

button.disabled
{

  opacity: 0.4;
  cursor: not-allowed;
}







@media (max-width: 700px){

.contacts-us form{
justify-content: space-around;
width: 80%;

}
  
  .contacts-us form div.flex{
 
   
    align-items: flex-start;



  }
  .submit
{

padding: 0.75rem 5rem;
  margin-left: 103px;
 
}

.contacts-us .animation
{
display: none;
}


#email , #message
{
  margin-left: 0px;
 width: 90%;

 padding: 0.7rem 0rem;
 margin-top: 1rem;
}




}



@media (max-width: 900px){

.animation1 .icon-envelope
{
  display: none;

  
}

}

.animation1 .icon-envelope
{
  color: var(--subtitle);
    
  
  font-size: 16.8rem;
  
}

.animation1 
{
  
    
length:18rem;
 height: 18rem;
  
}


.animation1 .icon-envelope:hover
{

  color: var(--blue);
  
   rotate:10deg;
   scale: 2.01;
   cursor: pointer;


}

.animation1 .icon-envelope
{
 transition: 0.6s;
color: var(--title);


}



@keyframes mymove1 {
0%  {scale: 0;}
60% {scale:1.2;}
100% {scale:1;}


}

.animation1 .icon-envelope
{
animation:mymove1 0.7s 1;

margin-left: auto;
margin-right: auto;
}

