
/* الشرح بعدين */













header ul
{

    gap:16px;


}


header ul li a 
{

    color: var(--title);
    opacity: 0.9;
    font-size: 0.88rem;
    font-weight: 500;
    
}



header ul li a:hover 
{

    color: var(--blue);
    opacity: 1;
    font-size: 0.9rem;
    font-weight: 500;
    transition: 0.3s;
}



header button
{
    font-size: 1.2rem;
}


header
{
 margin-top: 1.2rem;
justify-content: space-between;
padding: 0 2.4rem;
justify-content: center;
}




header nav 
{
    background-color: var(--bgheader);
    padding: 0.77rem 1.6rem;
    border-radius: 55px;
    box-shadow: 1px 1px 10px 2px rgb(49,  48, 48 , 0.40);
    margin-left: auto;
    margin-right: auto;
}





header button.menu
{
    display: none;
}

header .fixed
{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgb(40,  40, 48 , 0.90);
   
    backdrop-filter: blur(4px);
}







header ul.model
{
animation:mymove 0.7s 1;
width: 75%;
margin-left: auto;
margin-right: auto;
margin-top: 2rem;
border-radius: 1rem;
padding: 1rem 2rem ;
background-color: var(--secondary);
}








header ul.model li
{
border-bottom:1px solid var(--border);
padding-bottom: 0.77rem;
padding-top: 0.5rem;
}


header ul.model li a
{
font-size: 1rem;

}


header ul.model li:last-child
{
border: none;
;
}


header ul.model li:first-child
{
border: none;
padding-bottom: 0rem;
padding-top: 0.2rem;
}




header .fixed ul.model li:first-child
{
text-align: right;
border: width border-style color;
margin-bottom: -1rem;
}


.mode
{
    background-color: var(--bgheader);
    height: 2.4rem;
    width: 2.4rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid salmon;
    color: var(--subtitle);
    transition: 0.5s;

}


 header.flex button.icon-link
{
    background-color: var(--bgheader);
    height: 2.4rem;
    width: 2.4rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid salmon;
    color: var(--subtitle);
    transition: 0.5s;

}

header.flex button.icon-link:hover
{
      background-color: var(--bgheader);
    height: 2.4rem;
    width: 2.4rem;
    border: 1px solid sandybrown;
   box-shadow: 1px 1px 7px 1px sandybrown;
    color: wheat;
}

.mode:hover
{
    background-color: var(--bgheader);
    height: 2.4rem;
    width: 2.4rem;
    border: 1px solid sandybrown;
   box-shadow: 1px 1px 7px 1px sandybrown;
    color: wheat;
}




.icon-cross
{
 transition: 0.3s;
color: var(--subtitle);


}


.icon-cross:hover
{

color:crimson;
font-size: 1.3rem;
rotate:180deg;
}




@keyframes mymove {
0%  {scale: 0;}
60% {scale:1.2;}
100% {scale:1;}


}


.icon-sun
{



    color: darkgoldenrod;
}















@media (max-width: 700px)
{

  header nav {display: none;}

  header button.menu
{
    display: block;
}


header
{

justify-content: space-between;

}

}