

footer 
{

justify-content: space-between;
padding-bottom: 1.9rem;



}

footer.flex ul li a
{

color: var(--subtitle);
font-size: 0.9rem;
transition: 0.4s;
}

footer.flex ul li a:hover
{

color: var(--blue);
font-size: 1rem;
font-weight: 500;

}



footer.flex ul
{

display: flex;
align-items: center;
gap: 1.9rem;

}



footer.flex p
{

color: rgb(133, 113, 122)



}



@media(max-width:800px){

footer{

 flex-direction: column;
 gap:2rem;
}

}