
.o{


 background: linear-gradient(lightskyblue, lightgray);
 flex-direction: column;
 border-radius: 12px;
 box-shadow: 2px 1px 7px 1px gray;
 border: 1px solid lightseagreen;

width: 500px;

}
.header1
{

display: flex;
flex-direction: column;
align-items: center;
gap:30px;

margin-top: 30px;

}

.header1 .text{


    color: #343434;
    font-size: 48px;
    font-weight: 700;
}

.header1 .underline
{
 box-shadow: 1px 1px 17px 4px gray;
width: 61px;
height: 6px;
background: #343434;
border-radius: 9px;


}

.inputs
{

margin-top: 55px;

display: flex;
flex-direction: column;
gap: 25px;

}
.input
{
 box-shadow: 5px 4px 7px 1px grey;
margin-top: 55px;

display: flex;
margin: auto;
width: 480px;
height: 80px;
border-radius: 6px;
background: #eaeaea;
align-items: center;


}

.input span
{
 
 margin: 0px 30px;



}



.input spani.con-cross
{
 

font-size: 300px;

}

.input input

{
height: 50px;
width: 400px;
background: #eaeaea;
border: none;
outline: none;
color: #343434;
font-size: 19px;


}



.forgot-password
{


    padding-left: 62px;
    margin-top: 27px;
    color:  #121212;
    font-size: 18px;
}


.submit-contaainer
{

display: flex;
justify-content: space-between;

border-radius: 100px;
cursor: pointer;


}

.submit-contaainer .login
{
 box-shadow: 8px 4px 17px 1px wheat;

    
}

.gray
{

 
    background: #eaeaea;

    color: #676767;
}
.input .icon-link
{

 font-size: 20px;
    color: #343434;
}
