

.left-section
{


    flex-grow: 1;
}

section.hero{
 min-height: 88px;
    margin-top: 3rem;
   
}



.avatar
{

width: 88px;
border-radius: 50%;
border: 1px solid darkorange;
padding: 2px;
box-shadow: 2px 2px 40px gray inset;
gap: 0.6rem;
}




.icon-verified
{
color: var(--blue);
font-size: 1.1rem;margin-bottom: 0.3rem;


}




.parent-avaar
{


    align-items: flex-end;
}




.all-icons
{


margin: 10px

}


.all-icons div
{
font-size: 1.2rem;
margin: 10px;
color: var(--subtitle);

}


.all-icons div:hover
{
 cursor: pointer;
transition: 0.3s;
font-size: 1.3rem;
color: var(--title);
}





.hero h1
{
    margin: 24px 0;
    line-height: 3.3rem;
    font-size: 2.85rem;
    color: var(--title);
}

.hero p
{
    margin-bottom: 32px ;
    line-height: 1.65rem;
    font-size: 0.9rem;
    color: var(--subtitle);
}


.animation
{


    width: 160px;
    height: 140px;
  
}


@media (max-width: 900px){

.animation
{
  display: none;
}

}



.right-section1 
{
justify-content: center;
align-items: center;

margin-left: 7rem;
font-size: 8rem;
 box-shadow: 1px 1px 7px 1px var(--title);
color:  var(--title);
transition: 0.9s;
}

.icon-arrow-right2
{


}



.right-section1:hover
{

font-size: 9rem;
 box-shadow: 1px 1px 7px 1px var(--blue);
color:  var(--blue);
}

