
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');

html
{
  font-family: 'Roboto' ;
  scroll-behavior: smooth;
}


*{
  margin: 0;
  padding: 0;
}







:root{

  --primary: #000;
  --secondary: rgb(24 24 27);
  --border: rgb(63, 63, 70 , 0.4);
  --title: rgb(244 244 245);
  --subtitle: rgb(161 161 170);
  --bgheader: rgb(39 39 42);
  --blue: rgb(93 ,188 ,252);

  --container-padding: 4.8rem;

}



.light
{

  --primary: rgb(250, 250, 250);
  --secondary: rgb(270, 270, 270 );
  --border: rgb(202 ,202, 202 , 0.5);
  --title: rgb(39 39 42);
  --subtitle: rgb(82 82 91);
  --bgheader: rgb(253 ,253 ,253);
  --blue: rgb(0 ,149 ,246);

}



body{

background-color: var(--primary);
color: white;

}


.container
{
padding: var(--container-padding);
border: 1px solid var(--border);
width: 80%;
margin-left: auto;
margin-right: auto;
background-color:  var( --secondary);


}


.divider
{
border-bottom: 1px solid var(--border);
margin: 3rem 0;
width: calc(100%  +  var(--container-padding) + var(--container-padding));
margin-left: calc(var(--container-padding)*-1);

}


li{
  list-style: none
}



a{
  text-decoration: none;
  color: initial
}


.flex
{
  display: flex;
 align-items: center;

}




.border
{

border: 2px solid red;


}

button{
  all: unset;
}


button:hover{
  cursor: pointer;

}


button:active{
 scale: 0.9;

}


@media(max-width: 1000px){

.container{
  --container-padding: 1.7rem;

  width: 90%;
  padding: var(--container-padding);
}

.divider
{
 width: calc(100% + var(--container-padding) + var(--container-padding));
 margin-left:   calc(var(--container-padding)*-1);
}

}






@media(max-width:600px){

.container{

  width: 98%;
  padding: 0 var(--container-padding);
}

}





::-webkit-scrollbar{

width: 1.3rem;

}
::-webkit-scrollbar-track{

  border: 7px solid #232c3b;
  box-shadow: inset 0 0 2.5px 2px rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
  background-color: var(--blue);
}
::-webkit-scrollbar-thumb{


background: linear-gradient(#232943 , #000 );
border-radius: 1rem;

  border: 1px solid var(--blue);
}
::-webkit-scrollbar-thumb:hover
{


background-color: linear-gradient(270deg,#232943 , #172643 );

}





.scroll2top 
{
height: 2.6rem;
width: 2.6rem;
background-color :skyblue;
border-radius: 50px;
position: fixed;
bottom: 2rem;
right: 3%;
text-align: center;
border: 2px solid wheat;
font-size: 1.7rem;
transition: 0.3s;


}

.scroll2top:hover 
{

background-color:var(--subtitle);

}




@media(max-width:800px){

.scroll2top{
opacity: 0.6y;

height: 2.2rem;
width: 2.2rem;
}

}
